@use '../util/' as *;

.container {
  height: 100vh;
  width: 100%;
  background: 
    url(../../../images/pattern-background-mobile.svg)
    no-repeat
    top center / 100% 50%
    var(--primary-pale-blue)
    ;

  @include breakpoint(desktop) {
    background: 
      url(../../../images/pattern-background-desktop.svg)
      no-repeat
      top center / 100% 50%
      var(--primary-pale-blue)
      ;
  }
}

