html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-red-hot);
  background: var(--dark-bg);
  color: var(--dark-text-2);
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
  font-family: var(--font-red-hot);
}

:root {
  --primary-pale-blue: hsl(225, 100%, 94%);
  --primary-bright-blue: hsl(245, 75%, 52%);
  --neutral-pale-blue: hsl(225, 100%, 98%);
  --neutral-desat-blue: hsl(224, 23%, 55%);
  --neutral-dark-blue: hsl(223, 47%, 23%);
}

:root {
  --font-red-hot: "Red Hat Display", sans-serif;
}

.container {
  height: 100vh;
  width: 100%;
  background: url(../../../images/pattern-background-mobile.svg) no-repeat top center/100% 50% var(--primary-pale-blue);
}
@media (min-width: 90em) {
  .container {
    background: url(../../../images/pattern-background-desktop.svg) no-repeat top center/100% 50% var(--primary-pale-blue);
  }
}

.header {
  height: 10.9375rem;
  background: url(../../../images/illustration-hero.svg) top center/cover no-repeat;
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
}

.card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20rem;
  height: 32.8125rem;
  border-radius: 0.9375rem;
  box-shadow: 1px 1px 20px -10px #000000;
  background: #fff;
}
.card .main .text-content {
  text-align: center;
}
.card .text-content h1 {
  padding-top: 1.875rem;
  font-size: 1.25rem;
  font-weight: 900;
  color: var(--neutral-dark-blue);
}
.card p {
  line-height: 1.75;
  color: var(--neutral-desat-blue);
  font-size: 0.875rem;
  font-weight: 500;
  padding: 0 2.5rem;
}
.card .plan-details {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.625rem 1.25rem;
}
.card .plan-details .rate h2, .card .plan-details p {
  font-size: 0.875rem;
  padding: 0;
  margin: 0;
}
.card .plan-details .rate h2 {
  color: var(--neutral-dark-blue);
  font-weight: 900;
}
.card .plan-details a {
  font-size: 0.75rem;
  font-weight: 700;
}
.card .plan-details a:visited {
  color: var(--neutral-dark-blue);
}
.card .plan-details a:hover {
  color: var(--neutral-desat-blue);
  cursor: pointer;
}
.card .buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 0.625rem;
}
.card .buttons button {
  font-family: var(--font-red-hot);
  width: 80%;
  padding: 0.9375rem;
  font-weight: 700;
  letter-spacing: 0.03125rem;
  border-radius: 0.625rem;
  margin: 0 auto;
  margin-bottom: 1.25rem;
  background-color: var(--primary-bright-blue);
  border: none;
  color: var(--neutral-pale-blue);
}
.card .buttons button:hover {
  opacity: 0.75;
  cursor: pointer;
}
.card .buttons div a {
  font-size: 0.875rem;
  font-weight: 900;
  text-decoration: none;
  color: var(--neutral-desat-blue);
}
.card .buttons div a:hover {
  color: var(--neutral-dark-blue);
}