@use '../util/' as *;

.card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: rem(320);
  height: rem(525);
  border-radius: rem(15);
  box-shadow: 1px 1px 20px -10px #000000
;
  background: #fff;

  .main .text-content {
    text-align: center;
  }

  .text-content h1 {
    padding-top: rem(30);
    font-size: rem(20);
    font-weight: 900;
    color: var(--neutral-dark-blue);
  }

   p {
    line-height: 1.75;
    color: var(--neutral-desat-blue);
    font-size: rem(14);
    font-weight: 500;
    padding: 0 rem(40);
  }

  .plan-details {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: rem(10) rem(20);

    .rate h2, p {
      font-size: rem(14);
      padding: 0;
      margin: 0;
    }

    .rate h2 {
      color: var(--neutral-dark-blue);
      font-weight: 900;
    }

    a {
      font-size: rem(12);
      font-weight: 700;
    }

    a:visited {
      color: var(--neutral-dark-blue);
    }

    a:hover {
      color: var(--neutral-desat-blue);
      cursor: pointer;
    }
  }

  .buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: rem(10);
    
    button {
      font-family: var(--font-red-hot);
      width: 80%;
      padding: rem(15);
      font-weight: 700;
      letter-spacing: rem(0.5);
      border-radius: rem(10);
      margin: 0 auto;
      margin-bottom: rem(20);
      background-color: var(--primary-bright-blue);
      border: none;
      color: var(--neutral-pale-blue);
    }

    button:hover {
      // background-color: var(--primary-pale-blue);
      opacity: 0.75;
      cursor: pointer;
    }
    
    div a {
      font-size: rem(14);
      font-weight: 900;
      text-decoration: none;
      color: var(--neutral-desat-blue);
    }

    div a:hover {
      color: var(--neutral-dark-blue);
    }
  }

}