html {
  font-size: 100%;
  box-sizing: border-box;
}

*, 
*::before, 
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-red-hot);
  background: var(--dark-bg);
  color: var(--dark-text-2);
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
  font-family: var(--font-red-hot);
}